@import 'reset';

.container {
    max-width: 1600px;
    height: auto;
    @media (max-width: 1200px) {
        max-width: 1200px;
    }
}
main {
    .news {
        background-image: url('../img/news/background.png');
        padding: 120px 0;
        @media (max-width: 1200px) {
            padding: 60px;
        }
        @media (max-width: 768px) {
            max-width: 768px;
            padding: 20px 30px;
        }
        @media (max-width: 1200px) {
            padding: 20px 10px;
        }
        hr {
            width: 100px;
            background-color: white;
        }
        h2 {
            color: #FFF;
            text-align: center;
            font-family: Montserrat;
            font-size: 44px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            margin-bottom: 90px;
            margin-top: 30px;
        }
        .slider {
            width: 100%;
            max-width: 1370px;
            height: 550px;
            margin: 0 auto;
            position: relative;
            padding: 0 100px;
            @media (max-width: 425px) {
                max-width: 425px;
                padding: 0 30px;
            }
            @media (max-width: 768px) {
                max-width: 768px;
                padding: 0 60px;
            }
         
            .news-slider {
                width: 100%;
                max-width: 1170px;
                height: 514px;
                .news-slider-wrapper {
                    width: 100%;
                    .swiper-slide {
                        background-color: white;
                        width: 100%;
                        height: 100%;
                        max-width: 370px;
                    }

                }
            }
            .swiper-button-prev, .swiper-button-next {
                width: 40px;
                height: 40px;
                color: #FFF;
                border-radius: 50%;
                border: 1px solid #FFF;
                &::after {
                    font-size: 10px;
                }
            }
            .swiper-pagination-bullet {
                background-color: inherit;
                border: 1px solid #FFF;
                opacity: 1;
                margin: -20px 15px;
                &-active {
                    background-color: #FFF;
                }
            }
        }
     
    }
    .gallery {
        padding: 150px 0 20px 0;
        height: auto;
        @media (max-width: 1200px) {
            padding: 60px;
        }
        @media (max-width: 768px) {
            max-width: 768px;
            padding: 20 30px;
        }
        @media (max-width: 1200px) {
            padding: 100px 10px;
        }
        hr {
            width: 100px;
            background-color: #2C4058;;
        }
        h2 {
            color: #2C4058;
            text-align: center;
            font-family: Montserrat;
            font-size: 44px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            margin-bottom: 90px;
            margin-top: 30px;
        }
        .gallery-wrapper {
            overflow: hidden;
            .content {
                max-width: 1170px;
                width: 100%;
                height: auto;
                margin: 10px auto;

                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 214px 214px;
                grid-template-areas: 'photo-1 photo-1 photo-2 photo-3'
                                     'photo-1 photo-1 photo-4 photo-5';
                gap: 10px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                img:nth-child(1){
                    grid-area: photo-1;
                }
                img:nth-child(2){
                    grid-area: photo-2;
                }
                img:nth-child(3){
                    grid-area: photo-3;
                }
                img:nth-child(4){
                    grid-area: photo-4;
                }
                img:nth-child(5){
                    grid-area: photo-5;
                }
            }
            .content-second {
                max-width: 1170px;
                width: 100%;
                height: auto;
                margin: 10px auto;
                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 214px 214px;
                grid-template-areas: 'photo-3 photo-2 photo-1 photo-1'
                                     'photo-5 photo-4 photo-1 photo-1';
                gap: 10px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                img:nth-child(1){
                    grid-area: photo-1;
                }
                img:nth-child(2){
                    grid-area: photo-2;
                }
                img:nth-child(3){
                    grid-area: photo-3;
                }
                img:nth-child(4){
                    grid-area: photo-4;
                }
                img:nth-child(5){
                    grid-area: photo-5;
                }
            }
        }
        a {
            display: block;
            margin: 76px auto;
            text-align: center;
            text-decoration: none;
            width: 190px;
            height: 50px;
            border: 2px solid #2C4058;
            padding: 16px 57px;
            color: #000;
            text-align: center;
            font-family: Montserrat;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            cursor: pointer;
            
        }
        
    }
}
